import React, { useState, useEffect } from 'react';
import posterImage from './Posterimg.png';
import { Box } from '@mui/material';

const Poster = () => {
  const [imageWidth, setImageWidth] = useState(0);
  const [imageHeight, setImageHeight] = useState(0);

  useEffect(() => {
    const handleResize = () => {
      const screenWidth = window.innerWidth;
      const imageAspectRatio = 1640 / 924;
      const width = 0.98 * screenWidth;
      const height = 0.75 * (width / imageAspectRatio);
      setImageWidth(width);
      setImageHeight(height);
    };

    handleResize(); // Call the handler initially

    window.addEventListener('resize', handleResize); // Add event listener for resize

    return () => {
      window.removeEventListener('resize', handleResize); // Cleanup the event listener on component unmount
    };
  }, []);

  return (
    <Box>
      <img
        src={posterImage}
        alt="Poster"
        style={{
          width: `${imageWidth}px`,
          height: `${imageHeight}px`,
          objectFit: 'cover',
        }}
      />
    </Box>
  );
};

export default Poster;
