import * as React from "react";
import { useState } from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Stack from "@mui/material/Stack";
import DeleteIcon from "@mui/icons-material/Delete";
import IconButton from "@mui/material/IconButton";
import Fingerprint from "@mui/icons-material/Fingerprint";

import { Avatar, Box, CardMedia, Button, Typography } from "@mui/material";
export default function DisclaimerCard() {
  const [show, setShow] = useState(true);
  const [dispvar, changeDisp] = useState("X");
  const shownot = () => {
    setShow(!show);
  };
  const changeDispval = () => {
    console.log(show);
    show ? changeDisp("") : changeDisp("X");
  };
  const onClickbut = () => {
    shownot();
    changeDispval();
    console.log(dispvar);
  };

  return (
    <Box sx={{ marginBottom: 1 }}>
      <Card sx={{ color: "white", backgroundColor: "Tomato" }}>
        <Stack direction="row">
          <Typography
            variant="h5"
            sx={{
              color: "white",
              align: "center",
              margin: 1,
              marginRight: 0,
            }}
          >
            {}
            Disclaimer
          </Typography>
          <Button
            onClick={() => {
              onClickbut();
            }}
            variant="outlined"
            sx={{
              color: "white",
              align: "center",
              margin: 1,
              border: 1,
              padding: 0,

              ":hover": { bgcolor: "pink", color: "black", border: "black" },
            }}
          >
            {" "}
            {dispvar}{" "}
          </Button>
        </Stack>

        {show && (
          <Card
            sx={{
              maxWidth: 1,
              backgroundColor: "White",
              color: "white",
              align: "center",
              margin: 1,
            }}
          >
            <CardContent>
              <Typography variant="body2" sx={{ color: "Black" }}>
                The process of computing income tax can prove to be a daunting
                and overwhelming task for the ordinary individual. This can
                largely be attributed to the intricate and convoluted nature of
                the income tax act, which encompasses numerous sections,
                deductions, and exemptions. To compound the difficulty, the
                government has recently introduced a new tax regime while
                retaining the existing one, thereby adding an additional layer
                of complexity to the already perplexing matter. The challenges
                associated with income tax calculations are so formidable that
                even professionals in the field, such as Chartered Accountants
                (CAs), often resort to outsourcing the computation of their own
                tax liabilities to other CAs. This serves as a testament to the
                arduous nature of the process, as even those with extensive
                knowledge and expertise in the field seek external assistance.
                In light of these difficulties, our website aims to provide a
                convenient and comprehensive solution. We offer a user-friendly
                platform that serves as a one-stop resource for computing your
                income tax payable, without the need to painstakingly read and
                decipher the various exemptions, sections, and deductions
                outlined in the income tax act. Furthermore, we go the extra
                mile by assisting you in determining your tax obligations and
                post-tax income under both the new tax regime and the old
                regime. This enables you to make a well-informed decision
                regarding the most advantageous approach for your specific
                circumstances. We emphasize the importance of choosing wisely,
                as the tax regime you select can have a significant impact on
                your financial situation. In summary, our website alleviates the
                burdensome process of computing income tax by offering a
                simplified and accessible solution. By utilizing our services,
                you can bypass the complexities of the income tax act and make
                informed decisions about your tax obligations, ensuring a
                smoother and more manageable experience.
              </Typography>
            </CardContent>
          </Card>
        )}
      </Card>
    </Box>
  );
}
